<template>
  <div class="allLike">
      <van-nav-bar title="我的收藏" left-text="返回" left-arrow :fixed="true" @click-left="back" />
      <div class="allLike-box">
        <div class="pro-card clearfix" v-for="(item,index) in allLikeData" :key="index" @click="goDetails(item.pid)">
          <van-swipe-cell>
            <van-cell :border="false">
              <div class="pro-img fl">
                <img class="auto-img" :src="item.smallImg" alt />
              </div>
              <div class="card-info fl">
                <div class="pro-info">
                  <div class="clearfix">
                    <div class="fl pro-name">{{item.name}}</div>
                    <div class="fl pro-rule">{{item.rule}}</div>
                  </div>
                  <div class="pro-enname">{{item.enname}}</div>
                </div>
                <div class="pro-price">
                  <div class="pro-price-text">￥{{item.price}}</div>
                </div>
              </div>
              <div class="goDetails">
                查看详情
              </div>
            </van-cell>
          </van-swipe-cell>
        </div>
      </div>
  </div>
</template>

<script>
import "../assets/css/allLike.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      allLikeData:[]
    };
  },
  created() {
    this.findAllLike()
  },
  methods: {
    //返回上一级
    back() {
      this.$router.go(-1);
    },
    //查询商品收藏
    findAllLike() {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == 2000) {
            if (result.data.result.length >= 1) {
              this.allLikeData = result.data.result
               
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    //点击卡片前往商品详情页面
    goDetails(pid){
      this.$router.push({name:"Details",query:{pid}})
    }
  },
};
</script>
